import React, { Component } from 'react';
import { MDBContainer, MDBRow, MDBCol, MDBCard, MDBCardBody, MDBInput, MDBBtn, MDBModalFooter } from 'mdbreact';
import '@fortawesome/fontawesome-free/css/all.min.css';
import 'bootstrap-css-only/css/bootstrap.min.css';
import 'mdbreact/dist/css/mdb.css';
import '../Css/SignIn.css';
import logo from '../Img/logo.png';
import Facebook from "../Components/Facebook.js";
import { Link, withRouter } from 'react-router-dom';
import Google from '../Components/Google';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Swal from 'sweetalert2'
import logo2 from '../Img/Isravisionlogo.png';
import logo3 from '../Img/Isravisionlogo1.png';
import logo4 from '../Img/Isravisionlogo2.png';
import Dialog from '@material-ui/core/Dialog';
import logoLast from '../Img/logoadvisor.png';

function Copyright() {
    return (
        <Typography variant="body2" color="textSecondary" align="center">
            {'Copyright  '}
            <Link color="inherit" href="#">
                IsraVisor
        </Link>{' '}
            {new Date().getFullYear()}
            {'.'}
        </Typography>
    );
}
class SignIn extends Component {
    constructor(props) {
        super(props)
        this.state = {
            email: "",
            password: "",
            checkBoxVal: "unCheck",
            rememberMe: false,
            lieceneNum: "",
            govILAsk: false,

        }
    }
    componentWillUnmount() {
        clearInterval(this.myInterval)
    }
    componentWillMount() {
        //  
        if (this.props.location.state !== undefined) {
            this.props.history.push({
                pathname: '/home/',
                state: { GuideTemp: this.props.location.state.GuideTemp }
            });
        }
    }
    //         -       
    componentDidMount() {
        const rememberMe = localStorage.getItem('rememberMe') === 'true';
        const usernameEntered = rememberMe ? localStorage.getItem('usernameEntered') : '';
        const PasswordEntered = rememberMe ? localStorage.getItem('PasswordEntered') : '';
        this.setState({
            email: usernameEntered,
            password: PasswordEntered,
            rememberMe: rememberMe,
            govILAsk: !this.state.govILAsk
        });
    }

    HandelEmailInput = (e) => {
        this.setState({
            email: e.target.value
        }
        )
    }
    HandelPasswordInput = (e) => {
        this.setState({
            password: e.target.value
        }
        )
    }
    HandelNumberInput = (e) => {
        this.setState({
            lieceneNum: e.target.value
        }
        )
    }
    RememberMe = () => {
        if (!this.state.rememberMe) {
            this.setState({ rememberMe: true });
            Swal.fire({
                position: 'center',
                icon: 'success',
                title: '     ',
                showConfirmButton: false,
                timer: 1200
            })
        }
        else {
            this.setState({ rememberMe: false });
            Swal.fire({
                position: 'center',
                icon: 'success',
                title: '      ',
                showConfirmButton: false,
                timer: 1200
            })
        }
    }
    Exit = () => {
        console.log("Exist");
        this.setState({
            govILAsk: false
        })
    }
    alertGuidesGovILAsk = () => {
        let licenseNum
        return (
            <MDBRow className="divGov">
                <h5>Do you have an account at the ministry of tourism  website?</h5>
                <MDBCol className="InputNumber" md="12">
                    <MDBInput
                        type="number"
                        onChange={this.HandelNumberInput}
                        label="Enter Your Liecnse Number"
                    />
                </MDBCol>
                <MDBCol className="ButtonClass" md="12">
                    <MDBCol className="ButtonClass" md="6">
                        <MDBBtn
                            type="button"
                            gradient="blue"
                            rounded
                            className="btn-block z-depth-1a"
                            onClick={this.SignInWithGovIL}>
                            Yes
                                            </MDBBtn>
                    </MDBCol>
                    <MDBCol className="ButtonClass" md="6">
                        <MDBBtn
                            type="button"
                            gradient="peach"
                            rounded
                            className="btn-block z-depth-1a"
                            onClick={this.Exit}>
                            No
                                            </MDBBtn>
                    </MDBCol>
                </MDBCol>


            </MDBRow>)
    }

    //             
    SignInFunc = () => {
        localStorage.clear();
        localStorage.setItem('rememberMe', this.state.rememberMe);
        localStorage.setItem('usernameEntered', this.state.rememberMe ? this.state.email : '');
        localStorage.setItem('PasswordEntered', this.state.rememberMe ? this.state.password : '');
        let signInUser = {
            Email: this.state.email,
            Password: this.state.password
        }
        //    APP.JS
        this.props.checkSignIn(signInUser);
    }

    SignInWithGovIL = () => {
        if (this.state.lieceneNum !== "") {
            this.props.GovList(this.state.lieceneNum);
        }
        else{
            Swal.fire({
                icon: 'error',
                title: 'Oops...',
                text: 'Please insert License Number!',
              })
        }
    }

    render() {
        return (
            <div className="Cont">
                {this.state.govILAsk ? <div className="aletGov">{this.alertGuidesGovILAsk()}</div> : null}
                <MDBContainer>
                    <MDBRow className="logoRow">
                        <MDBCol md="12 text-center" >
                            <img className="LogoDiv" alt="" src={logoLast} />
                        </MDBCol>
                    </MDBRow>
                    {this.state.govILAsk ? null  :  
                    <MDBRow className="RowDivSignIn text-center">
                        <MDBCol className="ColDivSignIn" md="6">
                            <MDBCard className="CardDivSignIn">
                                <MDBCardBody className="cardBody mx-4">
                                    <div className="text-center">
                                        <h3 className="dark-grey-text mb-5">
                                            <strong>Sign in</strong>
                                        </h3>
                                    </div>
                                    <div className="text-center mb-3">
                                        <MDBInput size="lg"
                                            label="Your email"
                                            value={this.state.email}
                                            group
                                            type="email"
                                            validate
                                            error="wrong"
                                            success="right"
                                            onChange={this.HandelEmailInput}
                                        />
                                        <MDBInput size="lg"
                                            label="Your password"
                                            value={this.state.password}
                                            group
                                            type="password"
                                            validate
                                            containerClass="mb-0"
                                            onChange={this.HandelPasswordInput}

                                        />
                                    </div>

                                    <div className="divRemember">
                                        <FormControlLabel
                                            control={<Checkbox id="rememberMe" value="lsRememberMe" checked={this.state.rememberMe} color="primary" />}
                                            label="Remember me"
                                            onChange={this.RememberMe}
                                        />
                                    </div>
                                    <div className="text-center mb-3 btnSignIn">
                                        <MDBBtn
                                            type="button"
                                            gradient="blue"
                                            rounded
                                            className="btn-block z-depth-1a"
                                            onClick={this.SignInFunc}
                                        >
                                            Sign in
                </MDBBtn>
                                    </div>
                                    <div>
                                        <div className="or-seperator"><i>or</i></div>

                                    </div>
                                    <MDBRow>
                                        <MDBCol md="6">
                                            <Facebook checkifExistFunc={this.props.checkIfexistUsers} Allusers={this.props.Allusers} PostGuideToSQLFromFacebook={this.props.PostGuideToSQLFromFacebook} />
                                        </MDBCol>
                                        <MDBCol md="6">
                                            <Google checkifExistFunc={this.props.checkIfexistUsers} Allusers={this.props.Allusers} PostGuideToSQLFromGoogle={this.props.PostGuideToSQLFromGoogle} />
                                        </MDBCol>
                                    </MDBRow>
                                    {/* <div>
                                        <div className="or-seperator"><i>or</i></div>

                                    </div>
                                    <MDBRow className="GOVDIV">
                                        <h5>Sign In By Liecense Number</h5>
                                        <MDBCol className="InputNumber" md="6">
                                            <MDBInput
                                                type="number"
                                                onChange={this.HandelNumberInput}
                                                label="Your Liecnse Number"
                                            />
                                        </MDBCol>
                                        <MDBCol className="ButtonClass" md="6">
                                            <MDBBtn
                                                type="button"
                                                gradient="blue"
                                                rounded
                                                className="btn-block z-depth-1a"
                                                onClick={this.SignInWithGovIL}
                                            >
                                                ENTER
                                            </MDBBtn>
                                        </MDBCol>


                                    </MDBRow> */}
                                </MDBCardBody>
                                <MDBModalFooter className="mx-5 pt-3 mb-1">
                                    <div className="row col-12">
                                        <div className="col-4 forgotPass">
                                            <p className="font-small blue-text d-flex justify-content-end"><Link to="/reset">Forgot Password?</Link></p>
                                        </div>
                                        <div className="col-8 signUp">
                                            <p className="font-small grey-text d-flex justify-content-end">
                                                Not a member?
                                                <Link to="/SignUp">SignUp</Link>
                                            </p>
                                        </div>
                                    </div>
                                </MDBModalFooter>
                            </MDBCard>
                        </MDBCol>
                    </MDBRow>
                    }
                    <Box mt={8}>
                        <Copyright />
                    </Box>
                </MDBContainer>
            </div>
        );
    }
}

export default withRouter(SignIn);

